.SMul{
  display: flex;
}
.SMul .SMli{
  position: relative;
  display: block;
  color: #666;
  font-size: 30px;
  height: 60px;
  width: 60px;
  background: #171515;
  line-height: 60px;
  border-radius: 50%;
  margin: 0 15px;
  cursor: pointer;
  transition: .5s;
  margin: auto;
}
.SMul .SMli:before{
  position: absolute;
  content: '';
  top: 0;
  left: 0;
  height: inherit;
  width: inherit;
  /* background: #d35400; */
  border-radius: 50%;
  transform: scale(.9);
  z-index: -1;
  transition: .5s;
}
.SMul .SMli:nth-child(1):before{
  background: #24292F;
}
.SMul .SMli:nth-child(2):before{
  background: #0A66C2;
}
.SMul .SMli:nth-child(3):before{
  background: #E1306C;
}
.SMul .SMli:nth-child(4):before{
  background: #2867B2;
}
.SMul .SMli:nth-child(5):before{
  background: #ff0000;
}
.SMul .SMli:hover:before{
  filter: blur(3px);
  transform: scale(1.2);
  /* box-shadow: 0 0 15px #d35400; */
}
.SMul .SMli:nth-child(1):hover:before{
  box-shadow: 0 0 15px #24292F;
}
.SMul .SMli:nth-child(2):hover:before{
  box-shadow: 0 0 15px #0A66C2;
}
.SMul .SMli:nth-child(3):hover:before{
  box-shadow: 0 0 15px #E1306C;
}
.SMul .SMli:nth-child(4):hover:before{
  box-shadow: 0 0 15px #2867B2;
}
.SMul .SMli:nth-child(5):hover:before{
  box-shadow: 0 0 15px #ff0000;
}
.SMul .SMli:nth-child(1):hover{
  color: #456cba;
  box-shadow: 0 0 15px #456cba;
  text-shadow: 0 0 15px #456cba;
}
.SMul .SMli:nth-child(2):hover{
  color: #26a4f2;
  box-shadow: 0 0 15px #0A66C2;
  text-shadow: 0 0 15px #0A66C2;
}
.SMul .SMli:nth-child(3):hover{
  color: #e23670;
  box-shadow: 0 0 15px #E1306C;
  text-shadow: 0 0 15px #E1306C;
}
.SMul .SMli:nth-child(4):hover{
  color: #2a6cbb;
  box-shadow: 0 0 15px #2867B2;
  text-shadow: 0 0 15px #2867B2;
}
.SMul .SMli:nth-child(5):hover{
  color: #ff1a1a;
  box-shadow: 0 0 15px #ff0000;
  text-shadow: 0 0 15px #ff0000;
}
.SMli a{
  text-decoration: none;
  color:#666;
}
.SMli .aGithub:hover{
  color:#666;
}
.SMli .aLinkedin:hover{
  color: #0A66C2;
}

